<style>
    @import './mallinformation.css';
</style>
<template>
	<div>
		<div class="contain" style="z-index: -1;">
			<div class="contai">
			<Tabs value="name1">
			<TabPane label="商城设置" name="name1">
<div style="background-color: #f0f0f0;height: 800px;">
  <div class="taboneleft">
    <div class="onepart">
    	<div class="parthead"><i class="ivu-icon ivu-icon-ios-cloud-upload-outline"></i>&nbsp网站LOGO</div>
    	<div style="margin-top: 80px;text-align: center;">
		<Upload :action="uplogourl" :headers="headers" :data="uplogodata">
        <Button type="ghost" icon="ios-cloud-upload-outline">上传网站Logo的照片</Button>
        </Upload>
        </div>
    	</div>
    <div class="twopart">
    	<div class="parthead"><i class="ivu-icon ivu-icon-ios-cloud-upload-outline"></i>&nbsp预约广告图</div>
    	<div style="margin-top: 80px;text-align: center;">
		<Upload :action="upappointmenturl" :data="upappointmentdata">
        <Button type="ghost" icon="ios-cloud-upload-outline">选择上传预约广告图</Button>
        </Upload>
        </div>
    </div>
    <div class="threepart">
    	<div class="parthead"><i class="ivu-icon ivu-icon-ios-cloud-upload-outline"></i>&nbsp店铺二维码</div>
    	<div style="margin-top: 80px;text-align: center;">
		<Upload :action="upqrcodeurl"  :data="upqrcodedata">
        <Button type="ghost" icon="ios-cloud-upload-outline">上传店铺二维码</Button>
       </Upload>     
       </div>
        <div style="height: 10px;"></div>
   </div>
  
</div>
<div class="forms">
	<div style="width: 400px;margin-left:200px; padding-top: 100px;">		
			<span style="padding-right:2%;">网站名称</span><Input v-model="webname" placeholder="请输入网站名称..." style="width: 325px"></Input><br /><br />		
    <span style="padding-right:2%;">经营范围</span><Input v-model="productname" placeholder="请输入经营范围..." style="width: 325px"></Input><br /><br />
	<span style="padding-right:2%;">联系人姓名</span><Input v-model="contacter" placeholder="请输入联系人..." style="width:300px"></Input><br /><br />
    <span style="padding-right:2%;">联系电话</span><Input v-model="tel" placeholder="请输入联系电话..." style="width:326px"></Input><br /><br />
    <span style="padding-right:2%;">预约手机</span><Input v-model="phone" placeholder="请输入预约手机..." style="width:326px"></Input><br /><br />
    <span style="padding-right:2%;">具体地址</span><Input v-model="loaction" placeholder="请输入具体地址..." style="width:326px"></Input><br /><br />
    <span style="padding-right:2%;">客服QQ</span><Input v-model="QQnum" placeholder="请输入客服QQ..." style="width:330px"></Input><br /><br />
    <div style="margin-top:20px;height: 200px;">
    	<Upload :action="upcardurl"  :data="upcarddata"><Button type="ghost" icon="ios-cloud-upload-outline">上传店铺名片</Button></Upload>
    </div>
    <div style="float: right;margin-top: 10px;">
    <Button size="large" type="info" @click="putinformations">保存修改</Button>	
    </div>
	</div>
</div>
</div>
				</TabPane>
				<TabPane label="系统设置" name="name2">
					<div style="height: 50px;"></div>
					<div class="bonelists">
						<ul>
							<li>
								<div>
									<span style="margin-left: 4%;padding-right:2%;">店铺欢迎语</span>
									<div style="display: inline-block;">
										<Input v-model="welcomeword" type="textarea" :autosize="{minRows: 3,maxRows: 8}" placeholder="请输入店铺欢迎语..." style="width: 300px"></Input>
									</div>
								</div>
							</li>
							<li>
								<div style="text-align: left;">
									<span style="margin-left: 4%;padding-right:2%;">网站关键字</span>
									<div style="display: inline-block;">
									<Input v-model="keyword" placeholder="请输入网站关键字..." style="width:290px" size="small"></Input>
									<!--<span class="tip">网站关键字，便于SEO</span>-->
								  </div>
								  </div>
							</li>							
							<li>
								<div style="text-align: left;">
									<span style="margin-left: 4%;padding-right:2%;">店铺的图片</span>
									<span style="display: inline;">
	<div class="demo-upload-list" v-for="item in uploadList">
        <template v-if="item.status === 'finished'">
            <img :src="item.url">
            <div class="demo-upload-list-cover">
                <Icon type="ios-eye-outline" @click.native="handleView(item.name)"></Icon>
                <!--<Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>-->
            </div>
        </template>
        <template v-else>
            <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
        </template>
    </div>
    <Upload
        ref="upload"
        :show-upload-list="false"
        :default-file-list="defaultList"
        :on-success="handleSuccess"
        :format="['jpg','jpeg','png']"
        :max-size="2048"
        :on-format-error="handleFormatError"
        :on-exceeded-size="handleMaxSize"
        :before-upload="handleBeforeUpload"
        multiple
        type="drag"
        :action="upshoptuurl"
        :data='upshoptudata'
        style="display: inline-block;width:58px;">
        <div style="width: 58px;height:58px;line-height: 58px;">
            <Icon type="camera" size="20"></Icon>
        </div>
    </Upload>
    <Modal title="View Image" v-model="visible">
        <img :src="downshoptuurl" v-if="visible" style="width: 100%">
    </Modal>
			</span>
					</div>
							</li>
							<li>
								<div style="float: right;">
							<Button size="large" type="info" @click="putinformations">保存修改</Button>
						</div>
							</li>
						</ul>
						
					</div>
				</TabPane>
			</Tabs>

		</div>
		</div>
	</div>
</template>
<script>
	//商铺的id用于修改
	var ids;
	var idx;
	export default {
		data() {
			return {
				webname: '',
				webtitle: '',
				productname: '',
				sorts: '',
				contacter: '',
				tel: '',
				phone: '',
				loaction: '',
				QQnum: '',
				name1: '',
				welcomeword: '',
				keyword:'',
				textareas: '',
				images:'',
				systemid:'',
				uplogourl:'',
				uplogodata:{
				    file:'logo',
				    type:0
				},
				upshoptuurl:'',
				upshoptudata:{
				    file:'header',
				    type:1
				},
				upappointmenturl:'',
				upappointmentdata:{
					file:'image',
				    type:2
				},
				upqrcodeurl:'',
				upqrcodedata:{
				    file:'qrcode',
				    type:3
				},
				upcardurl:'',
				upcarddata:{
				    file:'screenshot',
				    type:4
				},
				downshoptuurl:'',
			    headers: {
					'X-Requested-With': 'XMLHttpRequest',
					'ContentType': 'application/json;charset=UTF-8'
				},				
				defaultList: [{
						'name':'',
						'url': ''
				}					
				],
				imgName: '',
				visible: false,
				uploadList: []
			}
		},
		methods: {
			//获取当前页面店铺的数据
			requestinformations() {
			var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/cfgdatas'
				}).then(function(res) {
					$that.webname = res.data.data.storecfg.name;
					$that.productname = res.data.data.storecfg.scope;
					$that.contacter = res.data.data.storecfg.linkman;
					$that.phone = res.data.data.storecfg.reservationCall;
					$that.tel = res.data.data.storecfg.serviceCall;
					$that.loaction = res.data.data.storecfg.address;
					$that.QQnum = res.data.data.storecfg.serviceQq;
					$that.images = res.data.data.storecfg.logo;			
					$that.keyword=res.data.data.syscfg.seoKeywords;
					$that.welcomeword=res.data.data.syscfg.welcome;					
					$that.downshoptuurl=res.data.data.storecfg.header;
					$that.defaultList[0].url=$that.downshoptuurl;
				    $that.defaultList[0].name='header';
					$that.systemid=res.data.data.syscfg.id;
					ids=res.data.data.storecfg.id;
					idx=res.data.data.syscfg.id;
					//上传图片路径
					$that.uplogourl='/api/storecfgs/'+res.data.data.storecfg.id+'/files';
					$that.upappointmenturl='/api/storecfgs/'+res.data.data.storecfg.id+'/files';
					$that.upqrcodeurl='/api/storecfgs/'+res.data.data.storecfg.id+'/files';
					$that.upshoptuurl='/api/storecfgs/'+res.data.data.storecfg.id+'/files';
					$that.upcardurl='/api/storecfgs/'+res.data.data.storecfg.id+'/files';
				})
			},
		//修改商城信息
			putinformations() {
				var that=this
				this.axios({
					method: 'put',
				    url: '/api/storecfgs/'+ids,
                 data:{
                 	linkman:that.contacter,
                    name:that.webname,
                    scope:that.productname,
                    reservationCall:that.phone,
                    serviceCall:that.tel,
                    address:that.loaction,
                    serviceQq:that.QQnum
                    
                 },
				}).then(function(res) {
					console.log(res)
				})
			},
			
			//上传文件的函数配置
			handleView(name) {
				this.imgName = name;
				this.visible = true;
			},
			//删除展示的图片
			handleRemove(file) {
				const fileList = this.$refs.upload.fileList;
				this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
			},
			//操作成功显示的图片
			handleSuccess(res, file) {
				file.url = this.downshoptuurl;
				file.name = 'header';
				
			},
			//文件格式错误
			handleFormatError(file) {
				this.$Notice.warning({
					title: 'The file format is incorrect',
					desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
				});
			},
			//文件大小错误
			handleMaxSize(file) {
				this.$Notice.warning({
					title: 'Exceeding file size limit',
					desc: 'File  ' + file.name + ' is too large, no more than 2M.'
				});
			},
			//上传数量的限制 
			handleBeforeUpload() {
				const check = this.uploadList.length <2;
				if(!check) {
					this.$Notice.warning({
						title: 'Up to five pictures can be uploaded.'
					});
				}
				return check;
			}

		},
		beforeMount: function() {
			this.requestinformations();
		},
		mounted() {
			this.uploadList = this.$refs.upload.fileList;
			this.requestinformations();
		}
	}
</script>


