<style>
.redstar{
	color: red;
	margin-right: 2px;
	font-size: 16px;
}
.photo{
	background-color: deepskyblue;
	height: 300px;
	width: 50%;
}
.basis{
	height: 300px;
}


  
</style>
<template>
	<div class="contain">
		<div class="item-title">
		<div class="miaoshu">
		<h3>添加商品详情</h3>
		<h5>商品详情信息设置</h5>
		</div>
		</div>	
			<div class="basis">
				<div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>商品名称</span> 
				 <Input v-model="productname" placeholder="请输入商品名称..." style="width: 200px" size="small"></Input>
			   </div>
			   <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>商品品牌</span> 
				 <Select v-model="model1" style="width:200px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
			   </div>
			   <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>供应商</span> 
				 <Select v-model="model1" style="width:200px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
			    </div>
			    <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>市场价</span> 
				 <Input v-model="value" placeholder="市场价" style="width: 200px" size="small"></Input>
			    </div>	
			    <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>销售价</span> 
				 <Input v-model="value" placeholder="成本价" style="width: 200px" size="small"></Input>
			    </div>	
			    <br />
			    <div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>总库存</span> 
                 <Input v-model="value" placeholder="总库存" style="width: 200px" size="small"></Input>
			</div>
			</div>
	</div>
</template>

<script>
	export default {
        data () {
            return {
                value: '',
                model1:'',
                productname:'',
                cityList: [
                    {
                        value: 'New York',
                        label: 'New York'
                    },
                    {
                        value: 'London',
                        label: 'London'
                    }],                 
                    sites: []
                    ,
                     datas:'',
                     stylename:'',
                 
            }
        },
  methods: {
      getParams () {
        // 取到路由带过来的参数 
        let routerparams = this.$route.params.name
        // 将数据放在当前组件的数据内
        this.productname = routerparams
      },
       newstylecode(){
       	
    	this.sites.push({name:this.datas})
    },
    clonestyle(){
    	
     }
   }, 
    watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
      '$route': 'getParams'
    },
     mounted() {		
			this.getParams();
		}
  }
        
</script>




