<template>
	<div>
<div class="contain" style="z-index: -1;">    
		<div class="item-title">
		<div class="miaoshu">
		<h3>预约订单</h3>
		<h5>预约订单订单管理以及查询</h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">
			   <Select v-model="ypeople" size="small" style="width:150px;">
               <Option v-for="item in allsort" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>           
               <Select v-model="ystaue" size="small" style="width:100px">
               <Option v-for="item in allone" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <DatePicker type="daterange" placement="bottom-end" placeholder="请输入日期范围" style="width: 200px" size="small" v-model="timerange"></DatePicker>
               <Input v-model="value" placeholder="搜索词..." style="width: 150px" size="small"></Input>
               <Button type="info" size="small" @click="queryinformations">搜索</Button>
               <Poptip confirm title="您确认删除这些内容吗？" @on-ok="ok" @on-cancel="cancel" placement="right">
               <Button  size="small" type="warning">删除</Button>           
               </Poptip> 
               <Button @click="handleSelectAll('{{one}}')" size="small" type="error">{{statu}}</Button>             
			</div>			
			<Table border :columns="columns7" :data="data6" ref="selection" @on-selection-change="selected" height="500"></Table>
			<div class="paging">
			<Page :total="totalpage" :page-size="pageSize"  show-total  @on-change="changepage" :transfer='ifs'></Page>
			</div>
	<!--修改模态框开始-->
	<Modal v-model="modalx" title="修改预约信息" @on-ok="modelok" @on-cancel="modelcancel">
       <Form :model="formItem" :label-width="80">
       	<FormItem label="预约人">
            <Input v-model="yname" placeholder="请输入预约人姓名..."></Input>
        </FormItem>
        <FormItem label="电话">
            <Input v-model="ytel" placeholder="请输入预约电话..."></Input>
        </FormItem>
        <FormItem label="住房面积">
            <Input v-model="yarea" placeholder="请输入住房面积..."></Input>
        </FormItem>
        <FormItem label="状态">
            <Select v-model="statusnum">
                <Option value="0">未处理</Option>
                <Option value="1">商谈中</Option>
                <Option value="2">已下单</Option>
                <Option value="3">已取消</Option>
            </Select>
        </FormItem>
       </Form>
      </Modal>
      <!--模态框结束-->
		</div>
</div>
</div>
</template>
<script>
var arrsid;
var deleteid='';
var arrids;
var ids;
var repairid;
 export default {
        data () {
            return {
            	yname:'',
            	ytel:'',
            	yarea:'',
            	totalpage:0,
            	pageSize:10,
            	ifs:false,
            	ajaxHistoryData:[],
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
               },
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },              
                    {
                     title: '用户id',
                        key: 'userId',
                        align: 'center'	
                    },
                    {
                        title: '预约人',
                        key: 'name',
                        align: 'center'
                    },
                    {
                        title: '预约电话',
                        key: 'phone',
                        align: 'center'
                    },
                    {
                        title: '住房面积',
                        key: 'area',
                        align: 'center'
                    },
                     {
                        title: '预约时间',
                        key: 'created',
                        align: 'center'
                    },
                    {
                        title: '状态',
                        key: 'status',
                        align: 'center'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx = true;
                                            this.repair(params.index);
                                        }
                                    }
                                }, '修改'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index);
                                            this.radiodeleteinformations();
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data6: [],
                allsort:[
                    {
                        value: 'allsort',
                        label: '预约人 '
                    },
                    {
                        value: 'twosui',
                        label: '预约电话'
                    },
                ],
                allone:[
                    {
                        value: '0',
                        label: '未处理'
                    },
                    {
                        value: '1',
                        label: '已下单'
                    },
                    {
                        value: '3',
                        label: '商谈中'
                    },
                    {
                        value: '4',
                        label: '已取消'
                    }
                ],
                yes:'全选',
                no:'取消全选',
                ypeople:'',
                model2:'',
                ystaue:'',
                model4:'',
                statu:'全选',
                value:'',
                modalx: false,
                options2:'',
                arrs:[],
                statusnum:'',
                timerange:''
            }
        },
        methods: {
        	//页面单选删除
           remove(index){
            	deleteid=this.data6[index].id
                this.data6.splice(index, 1);
            },
           repair(index){
            	repairid=this.data6[index].id
            	console.log(repairid)
            },
             //数据单选删除
            radiodeleteinformations(){
         	 var a=[];
           	 a.push(deleteid)
         	 this.axios({
             method: 'delete',
             url: '/api/reservations/'+a,
         })     
            },
            //全选
            handleSelectAll (status) {
            	if (this.one==false) {
            		this.$refs.selection.selectAll(false);
            		this.statu=this.yes;
            		this.one=true
            	} else{
            		 this.$refs.selection.selectAll(true);
            		 this.statu=this.no;
            		 this.one=false;                 		 
            	}
               
            },       
            //多选选中的index值
            selected(selection){
            	arrsid={};
            	arrids=[];
	         	for(var i=0;i<=selection.length-1;i++){ 
	         		arrsid[selection[i].id]=true;
	         		arrids.push(selection[i].id)
	          }       
            },
            //提示框的取消与确认，多选数据的删除 
            ok(){          	
                var evens = _.filter(this.data6, function(n) {          
                	return !arrsid[n.id];                
                }); 
                this.data6=evens;           
         	    this.axios({
                method: 'delete',
                url: '/api/delreservations/'+arrids,
              })
                this.$Message.info('信息已经删除');                                     
            },
            cancel () {
                this.$Message.info('信息取消了删除');
            },
            modelok () {
          	    this.putinformations();
                this.$Message.info('Clicked ok');
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            },
            //获取预约信息
            requestinformations() {
				var that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/reservations'
				}).then(function(res) {
					that.data6=res.data.data;
					// 保存取到的所有数据     
					that.ajaxHistoryData=res.data.data;
                    that.totalpage = res.data.data.length;
                // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
                if(res.data.data.length < that.pageSize){
                    that.data6 = that.ajaxHistoryData;
                }else{
                    that.data6 = that.ajaxHistoryData.slice(0,that.pageSize);
                }
					console.log(res.data.data)
					for(var i=0;i<res.data.data.length;i++){
						 if (res.data.data[i].status==0) {
						 	res.data.data[i].status="未处理";
						 } else if(res.data.data[i].status==1){
						 	res.data.data[i].status="商谈中";
						 }else if(res.data.data[i].status==2){
						 	res.data.data[i].status="已下单";
						 }else{
						 	res.data.data[i].status="已取消";
						 }
					}									
					repairid=res.data.data.id
				})
			},
            // 修改预约信息
            putinformations() {
				var that=this
				this.axios({
				  method: 'put',
				  url: '/api/reservations/'+repairid,
                   data:{
                   name:that.yname,
                   phone:that.ytel,
                   area:that.yarea
                 },
				}).then(function(res) {
					console.log(res)
				}).catch(function(res){
					console.log(res)
				})
			},
			//按多条件查询
          queryinformations() {
           console.log(this.timerange)
		    var that=this
		    var names;
		    var phones;
            console.log(this.ypeople)
            if(this.ypeople=='allsort'){
            	 names=this.value
            }else if(this.ypeople=='twosui'){
            	 phones=this.value
            }else{
            	names=null;
            	phones=null;
            }
				this.axios({
					method: 'post',
				    url: '/api/filterreservation',
                    data:{
                    name:names,
                    phone:phones,
                    status:that.ystaue,
                    time1:that.timerange[0],
                    time2:that.timerange[1]
                 },
				}).then(function(res) {				
					that.data6=res.data.data.reservation
				})
			},
			// 获取历史记录信息
            changepage(index){
                var _start = ( index - 1 ) * this.pageSize;
                var _end = index * this.pageSize;
                this.data6= this.ajaxHistoryData.slice(_start,_end);
            }
        },
        mounted() {		
			this.requestinformations();
		}
    }
</script>




