<template>
	<div>
	<div class="contain" style="z-index: -1;">
		<div class="item-title">
		<div class="miaoshu">
		<h3>商品分类管理</h3>
		<h5>网站文章分类添加与管理</h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">
               <Input v-model="value" placeholder="请输入关键词" style="width: 150px" size="small"></Input>
               <Button type="info" size="small" @click="search">搜索</Button>
               <Button type="success" size="small" @click="modaladd=true">添加分类</Button>
			</div>
			<Table border ref="selection" :columns="columns7" :data="data6" height="500"></Table>
			<div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
	<!--修改分类-->
    <Modal  v-model="modalx" title="修改商品分类" @on-ok="ok" @on-cancel="cancel">
       <Form :model="formItem" :label-width="80">
       <FormItem label="分类名称">
            <Input v-model="repairname" placeholder="请输入分类名称..."></Input>
       </FormItem>    
        </Form>
    </Modal>
    <!--修改分类  jiesu -->
    <!--添加分类  开始 -->
    <Modal v-model="modaladd" title="添加新商品分类" @on-ok="addok" @on-cancel="addcancel">
       <Form :model="formItem" :label-width="80">
       	<FormItem label="分类名称">
            <Input v-model="addname" placeholder="请输入分类名称..."></Input>
        </FormItem>    
        </Form>
    </Modal>
    <!--添加分类  jiesu -->
		</div>
	</div>
	</div>
</template>

<script>
var arrsid;
var deleteid='';
var arrids;
var ids;
var repairid;
var repairarr;
import expandRow from '../goods/goodssorttwo';	
 export default {
 	components: { expandRow },
        data () {
            return {
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
               },
            	value:'',
                columns7: [
                    {
                        type: 'expand',
                        width: 50,
                        render: (h, params) => {
                            return h(expandRow, {
                                props: {
                                    row: params.row
                                }
                            })
                        }
                    },
                     {
                        type: 'index',
                        align: 'center',
                        title: '序号 ',
                        width:120                      
                     },
                    {
                        title: '分类名称',
                        key: 'name',
                        align: 'center',
                    },
                    {
                    	title: '是否显示',
                        key: 'disabled',
                        align: 'center',
                    },
                    {
                        title: '是否显示',
                        key: 'ifshow',
                        align: 'center'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'                                       
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx=true;
                                            this.repair(params.index);
                                        }
                                    }
                                }, '修改'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index);
                                            this.radiodeleteinformations();
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data6: [],           
                modalx: false,
                modaladd:false,
                repairname:'',
                addname:''
            }
        },
        methods: {
            remove (index) {
            	deleteid=this.data6[index].id
                this.data6.splice(index, 1);
            },
            repair(index){
            	this.repairname=this.data6[index].name;
            	repairid=this.data6[index].id
            },
            //数据单选删除
           radiodeleteinformations(){
         	 var a=[];
           	 a.push(deleteid)
         	 this.axios({
             method: 'delete',
             url: '/api/goodstypes/'+a,
           })     
           },
             //获取分类信息
            requestsort(){
				var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/goodstypes'
				}).then(function(res) {
					console.log(res)
         			$that.data6=res.data.data; 
         			$that.repairname='';
				})
			},
			//修改分类信息 
		   repairsort(){
			var that=this;
			this.axios({
				method:'put',
				headers:{
				'X-Requested-With': 'XMLHttpRequest',
				'ContentType': 'application/json;charset=UTF-8'
				},
				url:'/api/goodstypes/'+repairid,
				data:{
					name:that.repairname			
				}
			}).then(function(res){
				var goodsType=_.find(that.data6, function(o) {
					return o.id===repairid; 
				});
				if(goodsType){
					goodsType.name=that.repairname;
				}
			})
			},
			//添加分类信息 
		   addsort(){
			var that=this;
			this.axios({
				method:'post',
				headers:{
				'X-Requested-With': 'XMLHttpRequest',
				'ContentType': 'application/json;charset=UTF-8'
				},
				url:'/api/goodstypes/',
				data:{
					name:that.addname,
					sort:100,
					disabled:false,
					parentid:''
				}
			}).then(function(res){
				that.data6.unshift(res.data.data)
			})
			},
            //提示框的取消与确认
            ok(){
            	this.repairsort();
                this.$Message.info('信息已经修改');                                     
                },
            cancel () {
                this.$Message.info('信息取消了删除');
            },
            addok(){
            	this.addsort();
                this.$Message.info('信息已经删除');                                     
                },
            addcancel () {        	
                this.$Message.info('信息取消了删除');
            },
            modelok () {
           	   
                this.$Message.info('Clicked ok');
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            },
            search(){
            	var searchobj=this.value;
            	var evens = _.filter(this.data6, function(n) {          
                	return !searchobj;                
                });
                this.data6=evens;
               }
        },
         mounted() {		
			this.requestsort();
		}
    }
</script>

<style>

</style>


