<style>
.contain{
margin-left: 210px;
margin-right: 10px;
}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;  
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}
.item-title{
	height: 55px;
	line-height: 25px;
    white-space: nowrap;
    width: 98%;
    padding-top: 3px;
    margin: 20px 1%;
    border-bottom: solid 1px #E6E6E6;
}
.search{
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 30px;
	display: inline-block;
}
.listtittle{
	 font-size: 12px;
    font-weight: normal;
    line-height: 24px;
    color: #777;
    vertical-align: bottom;
    letter-spacing: normal;
    display: inline-block;
    padding: 0
}
.tips{
	color: #0ba4da;
}
.routerx{
	color: #fff;
}
.paging{
	margin-top: 50px;
	float: right;
	margin-right: 20px;
}
.center{
        width: 300px;
        margin: 10px auto;
        overflow: hidden;
    }
.center-right{
        float: right;
    }
.ivu-message{
    	z-index: 9999999999;
    }
</style>
<template>
	<div>
<div class="contain" style="z-index: -1;">
	    <div>
		<div class="item-title">
		<div class="miaoshu">
		<h3>商品管理</h3>
		<h5>商城所有商品索引及管理</h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">
			   <Select v-model="model1" size="small" style="width:150px;"placeholder="所有分类">
               <Option v-for="item in allsort" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <Select v-model="model2" size="small" style="width:100px" placeholder="所有品牌">
               <Option v-for="item in allbrand" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <Select v-model="model3" size="small" style="width:100px"placeholder="全部">
               <Option v-for="item in allone" :value="item.value" :key="item.value" >{{ item.label }}</Option>
               </Select>
               <Select v-model="model4" size="small" style="width:100px" placeholder="全部">
               <Option v-for="item in alltwo" :value="item.value" :key="item.value" >{{ item.label }}</Option>
               </Select>
               <Input v-model="value" placeholder="搜索词..." style="width: 150px" size="small"></Input>
               <Button type="info" size="small">搜索</Button>
               <Button type="success" size="small" @click="modaladd=true">添加商品</Button>          
               <Poptip confirm title="您确认删除这些内容吗？" @on-ok="ok" @on-cancel="cancel" placement="right">
               <Button  size="small" type="warning">删除</Button>           
               </Poptip> 
               <Button  size="small" type="primary"  @click="exportData(1)">导出</Button>
               <Button @click="handleSelectAll('{{one}}')" size="small" type="error">{{statu}}</Button>
			</div>			
			<Table border :columns="columns7" :data="data6" ref="table" @on-selection-change="selected" height="500"></Table>
	<!-- 修改模态框开始-->
       <Modal v-model="modalx" title="修改商品信息" @on-ok="modelok" @on-cancel="modelcancel">
	  <Tabs value="name1">
        <TabPane label="商品基础信息 " name="name1"><Form :model="formItem" :label-width="80">
       	<FormItem label="商品名称">
            <Input v-model="formItem.input" placeholder="请输入商品名称..."></Input>
        </FormItem>
        	<FormItem label="商品简介">
            <Input v-model="formItem.input" placeholder="请输入商品简介..."></Input>
        </FormItem>    
        	<FormItem label="商品货号">
            <Input v-model="formItem.input" placeholder="请输入商品货号..."></Input>
        </FormItem>
        	<FormItem label="商品分类">
            <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
				 <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
				 <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
        </FormItem>
        	<FormItem label="商品品牌">
            <Input v-model="formItem.input" placeholder="请输入商品品牌..."></Input>
        </FormItem>
        	<FormItem label="供应商">
            <Input v-model="formItem.input" placeholder="请输入供应商..."></Input>
        </FormItem>
        	<FormItem label="本店售价">
            <Input v-model="formItem.input" placeholder="请输入本店售价..."></Input>
        </FormItem>
        	<FormItem label="市场价">
            <Input v-model="formItem.input" placeholder="请输入市场价..."></Input>
        </FormItem>
        <FormItem label="成本价">
            <Input v-model="formItem.input" placeholder="请输入成本价..."></Input>
        </FormItem>
        <FormItem label="总库存">
            <Input v-model="formItem.input" placeholder="请输入总库存..."></Input>
        </FormItem>
        <FormItem label="销售量">
            <Input v-model="formItem.input" placeholder="请输入邮箱地址..."></Input>
        </FormItem>
        <FormItem label="上传图片">
        	 <Upload action="//jsonplaceholder.typicode.com/posts/">
             <Button type="ghost" icon="ios-cloud-upload-outline">上传商品图片</Button>
    </Upload>
        </FormItem>
        </Form>
        </TabPane>
        <TabPane label="商品类型" name="name2">标签二的内容</TabPane>
        <TabPane label="商品详情及图片" name="name3">标签三的内容</TabPane>
    </Tabs>    
        </Modal>
      <!--模态框结束-->
      <!-- 添加模态框开始-->
    <Modal v-model="modaladd" title="添加商品信息" @on-ok="modelok" @on-cancel="modelcancel">
    	<div id="" style="height:300px;">
    	<Form :model="formItem" :label-width="80">
       	<FormItem label="商品名称">
            <Input v-model="productname" placeholder="请输入商品名称..."></Input>
        </FormItem>
        	<FormItem label="商品分类">
            <Cascader :data="sortdata" v-model="sortvalue" style="width: 200px;"  @on-change="selectdata"></Cascader>		 
        </FormItem>
        </Form>
    	</div>   	  
        </Modal>
      <!--模态框结束-->
			<div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
</div>
</div>
</div>
</template>
<script>
import Vue from 'vue';
let _ = require("lodash");
var selectname;
var selectid;
import tableparts from '../Smallparts/tableparts'
 export default {
 	  components: {
     tableparts:tableparts
              },
  
        data () {
            return {
            	sortvalue: [],
            	sortdata: [{
                    value: 'beijing',
                    label: '北京',
                    children: [
                        {
                            value: 'gugong',
                            label: '故宫'
                        }                    
                    ]
                },
                        {
                    value: 'jiangsu',
                    label: '江苏',
                    children: [
                        {
                            value: 'nanjing',
                            label: '南京'                   
                        },
                        {
                            value: 'suzhou',
                            label: '苏州'                          
                        }
                    ],
                }],
            	formItem: {
                    input: ''
               },
                animal: '',
            	modalx: false,
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '商品名称',
                        key: 'name',
                         align: 'center'
                    },
                    {
                        title: '分类',
                        key: 'sort',
                         align: 'center'
                    },
                    {
                        title: '原价 ',
                        key: 'originalprice',
                         align: 'center'
                    },
                    {
                        title: '现价',
                        key: 'price',
                         align: 'center'
                    },
                     {
                        title: '上架时间',
                         key: 'uptime',
                         align: 'center',
                         width: 110,
                    },              
                     {
                        title: '创建时间',
                        key: 'created',
                         align: 'center',
                         width: 110,
                    },
                     {
                        title: '品牌',
                        key: 'logo',
                         align: 'center'
                    },
                    {
                        title: '上/下架',
                        key: 'updown',
                        align: 'center',
                        render: (h) => {
                        return h(tableparts,{
                        	props:{
                        		yes: '上架',
                                 no: '下架'
                        	}
                        });
                      }
                    },
                    {
                        title: '商品图片',
                        key: 'path',
                        align: 'center',
                        render:(h,params)=>{
                        	return h('img',{
                        		domProps:{
                                src:params.row.path},
                                style: {
                                        width: '100px',
                                        height:'50px'
                                       }                       		
                        	})
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 200,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                            h('Button', {
                                    props: {
                                        type: 'success',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '预览'),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx=true
                                        }
                                    }
                                }, '修改'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data6: [],
                allsort:[
                    {
                        value: 'allsort',
                        label: '所有分类'
                    },
                    {
                        value: 'twosui',
                        label: '0-2岁'
                    },
                ],
                allbrand:[
                    {
                        value: 'allbrand',
                        label: '所有品牌'
                    }
                ],
                allone:[
                    {
                        value: 'allsort',
                        label: '所有分类'
                    },
                    {
                        value: 'twosui',
                        label: '0-2岁'
                    },
                ],
                alltwo:[
                    {
                        value: 'allsort',
                        label: '所有分类'
                    },
                    {
                        value: 'twosui',
                        label: '0-2岁'
                    },
                ],
                yes:'全选',
                no:'取消全选',
                model1:'',
                model2:'',
                model3:'',
                model4:'',
                statu:'全选',
                value:'',
                arrs:[],
                modaladd:false,
                cityList:[],
                productname:''
            }
        },
        methods: {
            remove (index) {
                this.data6.splice(index, 1);
            },
            //全选
            handleSelectAll (status) {
            	if (this.one==false) {
            		this.$refs.table.selectAll(false);
            		this.statu=this.yes;
            		this.one=true
            	} else{
            		 this.$refs.table.selectAll(true);
            		 this.statu=this.no;
            		 this.one=false;                 		 
            	}
               
            },
            //请求商品列表数据
            requestgoods(){
				var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/goods'
				}).then(function(res) {
					$that.data6=res.data.data
				})
			},
			//查询全部商品分类
			requestgoodsort(){
				var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/goodstypes/type'
				}).then(function(res) {
					var tempData ={}					
					var datas = res.data.data;
					var m = 0;
					var len = datas.length;
					var tmp;
					var children;
					var idx=0;
					var finalData;
					var childrenArr;
					if(len==0){
						return;
					}
					var finalDatas=[];
					var parentArr=_.filter(datas,function(data){
						return !data.parentId && !data.disabled;
					});
					for(m;m<parentArr.length;m++){
						tmp = parentArr[m];
						children=[];						
						finalData ={
							value:tmp.id,
							label:tmp.name,
							children:children
						};
						finalDatas[m]=finalData;
						tempData[tmp.id]=children;
					}
					
					var childrenArr = _.filter(datas,function(data){
						return !!data.parentId && !data.disabled;
					});
					len=childrenArr.length;
					for(m=0;m<len;m++){
						tmp = childrenArr[m];
						finalData ={
							value:tmp.id,
							label:tmp.name
						};
						children = tempData[tmp.parentId];
						if(!children){
							continue;
						}
						children[children.length]=finalData;
					}
					$that.sortdata=finalDatas;
					console.log($that.sortdata)
				})
			},
			//
			selectdata(value,selectedData){
            selectid=selectedData[value.length-1].value;
            selectname=selectedData[value.length-1].label;
			},
			//添加商品
			addgoods(){
			var that=this;
			this.axios({
				method:'post',
				headers:{
				'X-Requested-With': 'XMLHttpRequest',
				'ContentType': 'application/json;charset=UTF-8'
				},
				url:'/api/goods',
				data:{
					name:that.formItem.input,
					goodsTypeId:selectid,
					disabled:true
				}
			}).then(function(res){
				console.log(res)
			})
			},

            //多选选中的index值
            selected(selection){
            	arrsid={};
	         	for(var i=0;i<=selection.length-1;i++){ 
	         		arrsid[selection[i].id]=true;
	          	}       
            	console.log(arrsid);
            },
            //提示框的取消与确认
            ok(){          	
                var evens = _.filter(this.data6, function(n) {          
                	return !arrsid[n.id];                
                }); 
                this.data6=evens;
                console.log(this.data6)
                this.$Message.info('信息已经删除');                                     
            },
            cancel () {
                this.$Message.info('信息取消了删除');
            },
            //模态框取消与确认
            modelok () {
            	this.addgoods();
                this.$Message.info('添加成功');
                setTimeout(() => {
                this.$router.push({ name: 'addgoods',query: {name:selectid}, params: {name:selectid}})
				}, 2000)
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            },
            //下面导出逻辑
              exportData (type) {
                if (type === 1) {
                    this.$refs.table.exportCsv({
                        filename: 'The original data'
                    });
                } else if (type === 2) {
                    this.$refs.table.exportCsv({
                        filename: 'Sorting and filtering data',
                        original: false
                    });
                } else if (type === 3) {
                    this.$refs.table.exportCsv({
                        filename: 'Custom data',
                        columns: this.columns8.filter((col, index) => index < 4),
                        data: this.data7.filter((data, index) => index < 4)
                    });
                }
            } 
        },
        mounted() {		
			this.requestgoods();
			this.requestgoodsort();
		}
    }
</script>




