import Vue from 'vue';
import Router from 'vue-router';
import goodslist from '@/components/goods/goodslist';
import upload from '@/components/goods/upload';
import goodssort from '@/components/goods/goodssort';
import Productspecification from '@/components/goods/Productspecification';
import Brandlist from '@/components/goods/Brandlist';
import Commentlist from '@/components/goods/Commentlist';
import orderlist from '@/components/order/Orderlist';
import addorder from '@/components/order/Addorder';
import invoice from '@/components/order/Invoice';
import refund from '@/components/order/Refund';
import returngoods from '@/components/order/Returngoods';
import orderlog from '@/components/order/Orderlog';
import addgoods from '@/components/goods/addgoods';
import memberlist from '@/components/members/memberlist';
import addmember from '@/components/members/addmember';
import mallinformation from '@/components/mall/mallinformation';
import administratorlist from '@/components/administrator/administratorlist';
import membersrank from '@/components/statistical/membersrank';
import salesituation from '@/components/statistical/salesituation';
import saledetails from '@/components/statistical/saledetails';
import reservations from '@/components/appointment/reservations';
import workbench from '@/components/workbench/workbench';
import mallmenu from '@/components/mall/mallmenu';
import userinformation from '@/components/userinformation/userinformation';
import login from '@/components/bone/login'
import layout from '@/components/bone/layout'
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: 'layout',
      name: 'layout',
      component: layout,
      meta:{keepAlive:true},
      children:[
                    { path: '/layout/goodslist', component: goodslist},
                    { path: '/layout/goodssort', component: goodssort},
                    { path: '/layout/workbench', component: workbench,name:'workbench'},
                    {
      path: '/layout/productspecification',
      name: 'productspecification',
      component: Productspecification
    },
    {
      path: '/layout/brandlist',
      name: 'Brandlist',
      component: Brandlist
    },
    {
      path: '/layout/Commentlist',
      name: 'Commentlist',
      component: Commentlist
    },
    {
      path: '/layout/orderlist',
      name: 'orderlist',
      component: orderlist
    },
    {
      path: '/layout/addorder',
      name: 'addorder',
      component: addorder
    },
    {
      path: '/layout/invoice',
      name: 'invoice',
      component: invoice
    },
    {
      path: '/layout/refund',
      name: 'refund',
      component: refund
    },
    {
      path: '/layout/returngoods',
      name: 'returngoods',
      component: returngoods
    },
    {
      path: '/layout/orderlog',
      name: 'orderlog',
      component: orderlog
    },
    {
      path: '/layout/addgoods',
      name: 'addgoods',
      component: addgoods
    },
    {
      path: '/layout/memberlist',
      name: 'memberlist',
      component: memberlist
    },
    {
    	path: '/layout/addmember',
      name: 'addmember',
      component: addmember
    },
     {
    	path: '/layout/mallinformation',
      name: 'mallinformation',
      component: mallinformation
    },
    {
    	path: '/layout/administratorlist',
      name: 'administratorlist',
      component: administratorlist
    },
    {
    	path: '/layout/membersrank',
      name: 'membersrank',
      component: membersrank
    },
     {
    	path: '/layout/salesituation',
      name: 'salesituation',
      component: salesituation
    },
       {
    	path: '/layout/saledetails',
      name: 'saledetails',
      component: saledetails
    },
     {
    	path: '/layout/reservations',
      name: 'reservations',
      component: reservations
    },
    {
    	path: '/layout/userinformation',
      name: 'userinformation',
      component: userinformation
    },
    {
    	path: '/layout/mallmenu',
      name: 'mallmenu',
      component: mallmenu
    }
               ]
    },
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/upload',
      name: 'upload',
      component: upload
    },
    
  ]
})



