<template>
	<div class="contain">
		<div style="padding-left: 300px;margin-top: 200px;">
		<Upload :before-upload="handleUpload" action="/api/storecfgs/{cfgId}/files" :data="updata">
            <Button type="ghost" icon="ios-cloud-upload-outline">选择需要上传的文件 </Button>
        </Upload>
         </div>
         <div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>商品名称</span> 
				 <Input v-model="productname" placeholder="请输入商品名称..." style="width: 200px" size="small"></Input>
			   </div>
			   <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4%;padding-right:2%;"><span class="redstar">*</span>商品品牌</span> 
				 <Select v-model="model1" style="width:200px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
			   </div>
			   <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4.5%;padding-right:2%;"><span class="redstar">*</span>供应商</span> 
				 <Select v-model="model1" style="width:200px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
			    </div>
			    <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4.5%;padding-right:2%;"><span class="redstar">*</span>市场价</span> 
				 <Input v-model="value" placeholder="市场价" style="width: 200px" size="small"></Input>
			    </div>	
			    <br />
			<div style="text-align: left;">
				 <span style="margin-left: 4.5%;padding-right:2%;"><span class="redstar">*</span>销售价</span> 
				 <Input v-model="value" placeholder="成本价" style="width: 200px" size="small"></Input>
			    </div>	
			    <br />
			    <div style="text-align: left;">
				 <span style="margin-left: 4.5%;padding-right:2%;"><span class="redstar">*</span>总库存</span> 
                 <Input v-model="value" placeholder="总库存" style="width: 200px" size="small"></Input>
			</div>
	</div>
</template>

<script>
	  export default {
        data () {
            return {
                file: null,
                loadingStatus: false,
                updata:{
                	file:null
                }
            }
        },
        methods: {
            handleUpload (file) {
                this.updata.file = file;             
            }
        }
    }
</script>

<style>
</style>


