<style>
.contain{
margin-left: 210px;
margin-right: 10px;
}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}
.item-title{
	height: 35px;
	line-height: 25px;
    white-space: nowrap;
    width: 98%;
    padding-top: 3px;
    margin: 20px 1%;
    border-bottom: solid 1px #E6E6E6;
}
.search{
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 30px;
	display: inline-block;
}
.listtittle{
	 font-size: 12px;
    font-weight: normal;
    line-height: 24px;
    color: #777;
    vertical-align: bottom;
    letter-spacing: normal;
    display: inline-block;
    padding: 0
}
.tips{
	color: #0ba4da;
}
.routerx{
	color: #fff;
}
.routerx:hover{
	color: #FFF;
}
</style>
<template>
	<div>
<div class="contain" style="z-index: -1;">    
		<div class="item-title">
		<div class="miaoshu">
		<h3>会员管理</h3>
		<h5>网站系统会员索引与管理</h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">		  
               <Input v-model="value" placeholder="搜索手机或者邮箱..." style="width: 150px" size="small"></Input>
               <Button type="info" size="small">搜索</Button>
               <Button type="success" size="small" @click="modaladd = true">添加会员</Button>
               <Button @click="handleSelectAll('{{one}}')" size="small" type="error">{{statu}}</Button>             
			</div>			
			<Table border :columns="columns7" :data="data6" ref="selection"></Table>
		<!-- 修改模态框开始-->
	    <Modal v-model="modalx" title="修改会员信息" @on-ok="modelok" @on-cancel="modelcancel">
        <Form :model="formItem" :label-width="80">
       	<FormItem label="会员昵称">
            <Input v-model="formItem.input" placeholder="请输入会员昵称..."></Input>
        </FormItem>
        <FormItem label="手机号码">
            <Input v-model="formItem.input" placeholder="请输入手机号码..."></Input>
        </FormItem>
        <FormItem label="邮箱地址">
            <Input v-model="formItem.input" placeholder="请输入邮箱地址..."></Input>
        </FormItem>
        <FormItem label="性别">
             <RadioGroup v-model="animal">
        <Radio label="保密"></Radio>
        <Radio label="男"></Radio>
        <Radio label="女 "></Radio>
            </RadioGroup>
        </FormItem>
        </Form>
        </Modal>
      <!--模态框结束-->
      <!-- 添加会员模态框开始-->
	    <Modal v-model="modaladd" title="添加会员" @on-ok="modelok" @on-cancel="modelcancel">
        <Form :model="formItem" :label-width="80">
       	<FormItem label="会员昵称">
            <Input v-model="formItem.input" placeholder="请输入会员昵称..."></Input>
        </FormItem>
        <FormItem label="手机号码">
            <Input v-model="formItem.input" placeholder="请输入手机号码..."></Input>
        </FormItem>
        <FormItem label="邮箱地址">
            <Input v-model="formItem.input" placeholder="请输入邮箱地址..."></Input>
        </FormItem>
        <FormItem label="性别">
        <RadioGroup v-model="animal">
        <Radio label="保密"></Radio>
        <Radio label="男"></Radio>
        <Radio label="女 "></Radio>
    </RadioGroup>
        </FormItem>
        
        </Form>
        </Modal>
      <!--模态框结束-->
      <div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
</div>
</div>
</template>
<script>
	var repairid;
 export default {
        data () {
            return {
            	options2:'',
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
               },
                animal: '爪哇犀牛',
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },

                    {
                        title: '会员昵称',
                        key: 'name',
                        align: 'center'
                    },
                    {
                        title: '角色',
                        key: 'role',
                        align: 'center'
                    },
                    {
                        title: '性别',
                        key: 'sex',
                        align: 'center'
                    },                  
                    {
                        title: '注册日期 ',
                        key: 'created',
                        align: 'center'
                    },
                    {
                        title: '最后登陆时间 ',
                        key: 'lastLogin',
                        align: 'center'
                    },
                    {
                        title: '生日日期',
                        key: 'birthday',
                        align: 'center'
                    },
                    {
                        title: '状态',
                        key: 'state',
                        align: 'center'
                    },
                    {
                        title: '禁用/启用',
                        key: 'state',
                        align: 'center',
                        render: (h,params) => {
                        return h("iSwitch",{
                        	props:{
                        		size: "large",
                        		'true-value':1,
                        		'false-value':0,
                        		 value:params.row.state
                        	},                       
                        	on:{
                        		 'on-change':(res)=>{
                        		  this.repairs(params.index);
                        		  this.memberifshows(res);
                        	   }
                        	},                    	                    	
                        },[
                        h('span', {
                           slot: 'open',
                         domProps:{
                           innerHTML: '启用'
                                   } 
                                    }),
                       h('span', {
                            slot:  'close',
                             domProps:{
                             innerHTML: '禁用'
                              } 
                         })                      
                        ]
                        )
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                            h('a',{
                            	attrs:{
                            		href:'#'
                            	}
                            },
                            [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx = true
                                        }
                                    }
                                }, '修改')]),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data6: [],
                allsort:[
                    {
                        value: 'allsort',
                        label: '所有分类'
                    },
                    {
                        value: 'twosui',
                        label: '0-2岁'
                    },
                ],
                allbrand:[
                    {
                        value: 'allbrand',
                        label: '所有品牌'
                    }
                ],
                allone:[
                    {
                        value: 'allsort',
                        label: '所有分类'
                    },
                    {
                        value: 'twosui',
                        label: '0-2岁'
                    },
                ],
                alltwo:[
                    {
                        value: 'allsort',
                        label: '所有分类'
                    },
                    {
                        value: 'twosui',
                        label: '0-2岁'
                    },
                ],
                yes:'全选',
                no:'取消全选',
                model1:'',
                model2:'',
                model3:'',
                model4:'',
                statu:'全选',
                value:'',
                modalx: false,
                modaladd: false,
                data6:[]
            }
        },
        methods: {
        	//获取修改id
        	repairs(index){
             repairid=this.data6[index].id
            },
           //获取会员信息
            getmember(){
           	var $that = this;
	            this.axios({
				 	method:'get',
				 	headers:{
				 		'X-Requested-With':'XMLHttpRequest',
				    	'ContentType':'application/json;charset=UTF-8'
				 	}, 		 	
				 url: '/api/members'	 	
				 }).then(function(res) {			 
				 	 for (var i=0;i<res.data.data.length;i++) {
				 	 	if(res.data.data[i].sex==0){
				 	 	   res.data.data[i].sex='女'
				 	 	}else{
				 	 	   res.data.data[i].sex='男'
				 	 	}
//				 	 	if (res.data.data[i].state==0) {
//				 	 		res.data.data[i].state=false;
//				 	 	} else{
//				 	 		res.data.data[i].state=true;
//				 	 	}
				 	 }
				 	       $that.data6=res.data.data;
				 })
            },
            remove (index) {
                this.data6.splice(index, 1);
            },
            handleSelectAll (status) {
            	if (this.one==false) {
            		this.$refs.selection.selectAll(false);
            		this.statu=this.yes;
            		this.one=true
            	} else{
            		 this.$refs.selection.selectAll(true);
            		 this.statu=this.no;
            		 this.one=false;                 		 
            	}
               
            },
            //控制会员是否禁用
            memberifshows(res){
            var result=res;
			this.axios({
				method:'put',
				headers:{
				'X-Requested-With': 'XMLHttpRequest',
				'ContentType': 'application/json;charset=UTF-8'
				},
				url:'/api/members/'+repairid,
				data:{
					state:result 
				}
			})
            },
            modelok () {
                this.$Message.info('Clicked ok');
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            }
        },
        mounted() {		
			this.getmember();
		}
    }
</script>




