<template>
	<div class="contain">
		<div class="item-title">
		<div class="miaoshu">
		<h3> 添加订单</h3>
		<h5>管理员在后台添加一个新订单</h5>
		</div>
		</div>
		<div class="bonelist">		
		   <ul >
			<li> 
				<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;"><span class="redstar">*</span>用户名</span> 
				 <Input v-model="username" placeholder="请输入用户名" style="width: 200px" size="small"></Input>				 			 
				<!-- <Button  size="small" type="info"><Icon type="ios-search-strong" size="16" @click="alerts()"></Icon>搜索</Button>-->
			     </div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;"><span class="redstar">*</span>收货人</span> 
				 <Input v-model="consignee" placeholder="请输入收货人..." style="width: 200px" size="small"></Input>
			     </div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;"><span class="redstar">*</span>手机号</span> 
				 <Input v-model="phonenum" placeholder="请输入手机号..." style="width: 200px" size="small"></Input>
			     </div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;"><span class="redstar">*</span> 用户地址</span> 
				 <Select v-model="prov" style="width:100px" size="small">
                 <Option v-for="item in arr" :value="item.name" :key="item.name" >{{ item.name }}</Option>
				 </Select>
				 <Select v-model="city" style="width:100px" size="small">
                 <Option v-for="item in cityArr" :value="item.name" :key="item.name" >{{ item.name }}</Option>
				 </Select>
				 <Select v-model="district" style="width:100px" size="small">
                 <Option v-for="item in districtArr" :value="item.name" :key="item.name" >{{ item.name }}</Option>
				 </Select>
				 <Input v-model="valuex" placeholder="请输入详细地址" style="width: 200px" size="small"></Input>
			     </div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;"><span class="redstar">*</span>配送物流</span> 
				 <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in logistics" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
			     </div></li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;"><span class="redstar">*</span>支付方式</span> 
				 <Select v-model="model2" style="width:100px" size="small">
                 <Option v-for="item in paystyle" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
			     </div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;">发票抬头</span> 
				 <Input v-model="payp" placeholder="发票抬头" style="width: 200px" size="small"></Input>
			     </div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;">添加商品</span> 
				  <Button  size="small" type="success" @click="alerts">添加商品</Button>
			</div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:2%;">管理员备注</span> 
				 <Input v-model="model4" placeholder="请输入备注..." style="width:400px" size="small"></Input>
			</div>
			</li>
			<li>
			<div style="text-align: left;">
				 <span style="margin-left: 14%;padding-right:5%;"></span> 
				<!-- <Button  size="large" type="info" @click="alerts">确认提交</Button>-->
				 <Button type="primary" :loading="loading" @click="toLoading">
                 <span v-if="!loading">确认提交</span>
                 <span v-else>Loading...</span>
                 </Button>
			</div>
			</li>
		</ul>									
		</div>
	</div>
</template>

<script>
	import {arrAll} from '../../common/js/sanjiliandong.js';
	export default {
        data () {
            return {
               loading: false,
               loading2: false,
               arr:arrAll,
               username:'',
               consignee:'',  //收货人     
               phonenum:'',
               model1:'',//选中物流
               model2:'',//选中支付方式
               model4:'',
               value:'',
               valuex:'',
               payp:'',
               //物流开始
               logistics:[
               {
               	value:'shentong',
               	label:'申通'
               },
                {
               	value:'zhongtong',
               	label:'中通'
               },
                {
               	value:'shunfeng',
               	label:'顺风'
               }
               ],
               //物流结束 
               //支付方式
               paystyle:[
               {
               	value:'zhifubao',
               	label:'支付宝'
               },
               {
               	value:'weixin',
               	label:'微信'
               }
               ],
               //支付方式结束 
		       prov: '北京',
		       city: '北京',
		       district:'东城区',
		       cityArr: [],
		       districtArr: []
            }
        },
        methods:{
        	toLoading () {
                this.loading = true;
           },       
        	alerts(){
        		var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/; 
        	if (this.username==''||this.consignee==''||this.phonenum==''||this.model1==''||this.model2=='') {
        		alert('请完善表单');
        		return false;
        	}else{
        		if(!phoneReg.test(this.phonenum)){
        		alert('请输入有效的手机号码！');  
                return false; 
        	}
        	}
        	
        	},
        	
        	updatecity(){
        		for (var i in this.arr) {
        			 var obj=this.arr[i];
        		if (obj.name==this.prov) {
        			this.cityArr=obj.sub;
        			break;
        		} 
        		}
        		this.city=this.cityArr[1].name;
        	},
        	
        	updateDistrict(){
        		for (var i in this.cityArr) {
        			var obj =this.cityArr[i];
        			if(obj.name==this.city){
        			this.districtArr = obj.sub;
					break;
        		  }
        		}
        		
        		if (this.district&&this.districtArr.length>0&&this.districtArr[1].name) {
        				this.district = this.districtArr[1].name;
        		} else{
        			this.district = '';
        	}
        	}
        },
        
        beforeMount: function () {
		this.updatecity();
		this.updateDistrict();
	},
	watch: {
		prov: function () {
			this.updatecity();
			this.updateDistrict();
		},
		city: function () {
			this.updateDistrict();
		}
	}
    }
</script>

<style>
.contain{
		margin-left: 210px;
		margin-right: 10px;
	}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}
	.item-title{
	height: 35px;
	line-height: 25px;
    white-space: nowrap;
    width: 98%;
    padding-top: 3px;
    margin: 20px 1%;
    border-bottom: solid 1px #E6E6E6;
}
.bonelist>ul>li{
	width: 100%;
   height: 60px;
   border:1px solid #F0F0F0;
   border-left: none;
   border-right:none ;
   border-top: none;
   line-height: 60px;
   padding-right: 50px;
}
.redstar{
	color: red;
	margin-right: 2px;
	font-size: 16px;
}
</style>


