<template>
<div class="contain" style="z-index: -1;">
		<div class="item-title">
		<div class="miaoshu">
		<h3>商品订单</h3>
		<h5>商城实物商品交易订单查询及管理</h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">
				<DatePicker type="daterange" placement="bottom-end" placeholder="下单时间" style="width: 200px" size="small"></DatePicker>
			    <Select v-model="paystatus" size="small" style="width:150px;" placeholder="支付状态">
                <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
               <Select v-model="paystyle" size="small" style="width:100px" placeholder="支付方式">
               <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <Select v-model="deliverystatus" size="small" style="width:100px" placeholder="发货状态">
               <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <Select v-model="orderstatus" size="small" style="width:100px" placeholder="订单状态">
               <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
                <Select v-model="consignee" size="small" style="width:100px" placeholder="收货人">
                <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
               <Input v-model="value" placeholder="搜索关键字" style="width: 150px" size="small"></Input>
               <Button type="info" size="small">搜索</Button>
               <Button  size="small" type="success">添加订单</Button>
			</div>			
		<Table border :columns="columns7" :data="data6" ref="selection"></Table>
			<!-- 修改模态框开始-->
	      <Modal v-model="modalx" title="修改商品信息" @on-ok="modelok" @on-cancel="modelcancel">
          <Form :model="formItem" :label-width="80">
       	  <FormItem label="商品价格">
            <Input v-model="formItem.input" placeholder="请输入商品价格..."></Input>
          </FormItem>
          <FormItem label="手机号码">
            <Input v-model="formItem.input" placeholder="请输入手机号码..."></Input>
          </FormItem>
          <FormItem label="邮箱地址">
            <Input v-model="formItem.input" placeholder="请输入邮箱地址..."></Input>
          </FormItem>
        <FormItem label="性别">
             <RadioGroup v-model="animal">
             <Radio label="保密"></Radio>
             <Radio label="男"></Radio>
             <Radio label="女 "></Radio>
            </RadioGroup>
        </FormItem>
        </Form>
        </Modal>
      <!--模态框结束-->
      <div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
		</div>
</div>
</template>
<script>
 export default {
        data () {
            return {
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
               },
                animal: '',
                columns7: [             
                    {
                        title: '订单编号',
                        key: 'name'
                    },
                    {
                        title: '收货人',
                        key: 'age'
                    },
                    {
                        title: '总金额',
                        key: 'address'
                    },
                    {
                        title: '应付金额',
                        key: 'address'
                    },
                    {
                        title: '订单状态',
                        key: 'address'
                    },
                    {
                        title: '支付状态',
                        key: 'address'
                    },
                    {
                        title: '发货状态',
                        key: 'address'
                    },
                    {
                        title: '支付方式',
                        key: 'address'
                    },
                    {
                        title: '配送方式',
                        key: 'address'
                    },
                     {
                        title: '下单时间',
                        key: 'address'
                    },                           
                       {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx=true
                                        }
                                    }
                                }, '修改'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                ordertime:'',
                paystyle:'',
                paystatus:'',
                deliverystatus:'',
                orderstatus:'',
                consignee:'',
                value:'',
                modalx: false,
                yes:'全选',
                no:'取消全选',
                statu:'全选',
                value:'',
                arrs:[],
                
                
                cityList:[
                  { value:'sad',
                   label:'sdsad'}
                ],
                data6: [
                    {
                        name: 'John Brown',
                        age: 18,
                        address: 'New Y'
                    },
                    {
                        name: 'Jim Green',
                        age: 24,
                        address: 'London'
                    },
                    {
                        name: 'Joe Black',
                        age: 30,
                        address: 'Sydneyrk'
                    },
                    {
                        name: 'Joe Black',
                        age: 30,
                        address: 'Sydney Nk'
                    },
                    {
                        name: 'Joe Black',
                        age: 30,
                        address: 'Sydney'
                    },
                    {
                        name: 'Jon Snow',
                        age: 26,
                        address: 'Ottawa '
                    }
                ]
            }
        },
        methods: {     
            remove (index) {
                this.data6.splice(index, 1);
            },
            handleSelectAll (status) {
                this.$refs.selection.selectAll(status);
            },
            alerts(){
            	alert(123)
            },          
            //模态框取消与确认
            modelok () {
                this.$Message.info('Clicked ok');
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            }
        }
    }
</script>

<style>
	.contain{
		margin-left: 210px;
		margin-right: 10px;
	}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}
.item-title{
	height: 35px;
	line-height: 25px;
    white-space: nowrap;
    width: 98%;
    padding-top: 3px;
    margin: 20px 1%;
    border-bottom: solid 1px #E6E6E6;
}
.search{
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 30px;
	display: inline-block;
}
.listtittle{
	 font-size: 12px;
    font-weight: normal;
    line-height: 24px;
    color: #777;
    vertical-align: bottom;
    letter-spacing: normal;
    display: inline-block;
    padding: 0
}
.tips{
	color: #0ba4da;
}
.paging{
	margin-top: 50px;
	float: right;
	margin-right: 20px;
}
</style>


