<template>
<div class="contain" style="z-index: -1;">
		<div class="item-title">
		<div class="miaoshu">
		<h3>评价管理 </h3>
		<h5>商品交易评价管理</h5>
		</div>
		</div>	
		<div class="functions">
			<div class="search">
               <Input v-model="value" placeholder="评论内容..." style="width: 150px" size="small"></Input>
               <Input v-model="value" placeholder="搜索用户..." style="width: 150px" size="small"></Input>
               <Button type="info" size="small">搜索</Button>            
			</div>
			
			<Table border :columns="columns7" :data="data6" ref="selection"></Table>
			<div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
</div>
</template>
<script>
 export default {
        data () {
            return {
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '用户',
                        key: 'name',
                        render: (h, params) => {
                            return h('div', [
                                h('Icon', {
                                    props: {
                                        type: 'person'
                                    }
                                }),
                                h('strong', params.row.name)
                            ]);
                        }
                    },
                    {
                        title: '评论内容',
                        key: 'age'
                    },
                    {
                        title: '商品',
                        key: 'address'
                    },
                     {
                        title: '评论时间',
                        key: 'age'
                    },
                    {
                        title: 'ip地址',
                        key: 'address'
                    },
                    {
                        title: '显示',
                        key: 'shuru'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '评论详情'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data6: [
                    {
                        name: 'John Brown',
                        age: 18,
                        address: '39.148.134.243',
                        shuru:'是'
                    },                   
                    {
                        name: 'Joe Black',
                        age: 30,
                        address: '39.148.134.243',
                        shuru:'是'
                    },
                    {
                        name: 'Jon Snow',
                        age: 26,
                        address: '39.148.134.243',
                        shuru:'是'
                    }
                ],
                yes:'全选',
                no:'取消全选',           
                statu:'全选',
                value:''
            }
        },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            }          
        }
    }
</script>

<style>
	.contain{
		margin-left: 210px;
		margin-right: 10px;
	}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}

.search{
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 30px;
	display: inline-block;
}
.listtittle{
	 font-size: 12px;
    font-weight: normal;
    line-height: 24px;
    color: #777;
    vertical-align: bottom;
    letter-spacing: normal;
    display: inline-block;
    padding: 0
}
.tips{
	color: #0ba4da;
}
</style>


